@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  background-color: #292B35;
}
.App {
font-family: 'Montserrat', sans-serif;
min-height: 100vh;
height: auto;
display: flex;
flex-direction: column;
align-items: center;
text-align: center;
grid-gap: 15px;
gap: 15px;
padding: 40px;
font-size: 16px;
/* background-image: linear-gradient(to top, #dfe9f3 0%, white 100%); */
color: white;
}

h1{
color: white;
}
input{
padding-left: 5px;
font-size: 20px;
width: 250px;
height: 30px;
color: white;
}

.form-control {
  background-color: rgb(56, 56, 56);
  color: white;
  padding: 20px;
}

.form-control:active {
  background-color: rgb(56, 56, 56);
  color: white;
}

.form-control::after {
  background-color: rgb(56, 56, 56);
  color: white;
}

.form-control::before {
  background-color: rgb(56, 56, 56);
  color: white;
}

.form-control:focus {
  background-color: rgb(56, 56, 56);
  color: white;
}
table{
width: auto;
border-collapse: separate;
border-spacing: 0 1em;
}
thead{
background-color: rgb(44, 44, 44);
color: white;
text-align: center;
max-width: 800px;
}
tbody > tr{
text-align: right;
}
.rank{
text-align: center;
font-weight: bold;
}
.logo{
display: flex;
justify-content: flex-start;
padding-left: 10%;
grid-gap: 10px;
gap: 10px;
}
.symbol{
text-align: center;
}

